
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
/*BASE FONTS*/
/* BASE COLORS */
/*NORMALIZE*/
* {
  outline: none !important;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
*:before, *:after {
  box-sizing: border-box;
}
html, body {
  width: 100%;
  min-width: 320px;
}
input, textarea {
  -webkit-appearance: none;
  border-radius: 0;
}
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button, textarea::-webkit-outer-spin-button, textarea::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number], textarea[type=number] {
  -moz-appearance: textfield;
}
input::-ms-clear, textarea::-ms-clear {
  display: none !important;
}
input::-ms-reveal, textarea::-ms-reveal {
  display: none !important;
}
figure {
  margin: 0;
}
button {
  padding: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
main {
  display: block;
}
ul,
ol,
nav {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
a {
  color: inherit;
  text-decoration: none;
  transition: 0.3s;
}
a:hover, a:focus {
  text-decoration: none;
}
img {
  max-width: 100%;
}
input, select, textarea, button {
  font-family: inherit;
}
body {
  font-family: "Roboto", "BlickMacSystemFont", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "sans-serif";
  font-size: 14px;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*CORE*/
.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: hidden;
  padding: 0 20px;
}
/*BUTTONS*/
.btn {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  transition: 0.15s ease-in-out;
  text-align: center;
  padding: 12px 25px;
  font-size: 16px;
  line-height: 20px;
  border-radius: 3px;
  border: 1px solid #000;
}
.btn-primary {
  background-color: #497599;
  border-color: #497599;
  color: #ffffff;
}
.btn-primary:hover {
  background-color: #416888;
  border-color: #416888;
  color: #ffffff;
}
.btn-primary:disabled, .btn-primary.disabled {
  background-color: #497599;
  border-color: #497599;
  color: #ffffff;
}
